* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lora', serif;
  text-transform: capitalize;
  outline: none;
  border: none;
  text-decoration: none;
  font-weight: normal;
}

html {
  font-size: 62.5%;
}

body {
  padding-left: 30rem;
  background-color: #0077b6;
}

section {
  padding: 1rem 5%;
  height: 52rem;
  width: 75rem;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, .3);
  border-radius: 1rem;
  overflow: hidden;
  margin: 1rem 0 1rem 9rem;
  background-color: #0077b6;


}

.btn {
  font-size: 2rem;
  padding: .7rem 4rem;
  background: rgba(255, 255, 255, .2);
  color: #fff;
  border-radius: 1rem;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, .2);
  margin: 2rem;
  cursor: pointer;
  transition: .2s linear;
}


.btn:hover {
  background: rgba(255, 255, 255, .5);
  color: #555;
}

.heading {
  font-size: 3rem;
  text-align: center;
  padding: 1rem;
  color: #fff;

}


