.about .content h3 {
    /* font-size: 2.5rem; */
    font-size: 1.5rem;
    color: #eee;
}

.about .content h3 span {
    color: #fff;
    font-size: 3rem;
}

.about .content p {
    font-size: 2rem;
    color: #eee;
    padding: .5rem 0;
}

.about .skills {
    /* padding: 1rem 0; */
    padding:0.5rem 0 ;
}

.about .skills .progress {
    /* margin: 1.5rem 0; */
    margin:1rem 0 ;
    /* padding: 1rem; */
    padding:0.5rem ;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, .2);
    background: rgba(255, 255, 255, .2);
    border-radius: 1rem;
}

.about .skills .progress h3 {
    display: flex;
    justify-content: space-between;
    /* font-size: 2rem; */
    font-size: 1.5rem;
    color: #fff;
}

.about .skills .progress .bar {
    position: relative;
    width: 100%;
    height: .3rem;
    margin: 1rem 0;
    background: #555;
}

.about .skills .progress .bar span {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #fff;
}

.about .skills .progress:nth-child(1) .bar span {
    width: 95%;
}

.about .skills .progress:nth-child(2) .bar span {
    width: 85%;
}

.about .skills .progress:nth-child(3) .bar span {
    width: 65%;
}

.about .skills .progress:nth-child(4) .bar span {
    width: 70%;
}