.address {
    padding: 1rem 0;
}

.address h3 {
    padding: 1rem;
    font-size: 25px;
    color: #fff;

}

.address h3 i {
    padding-right: .5rem;
}

.address p {
    font-size: 2rem;
    color: #eee;
    cursor: pointer;
}
 .contact .share {
    justify-content: center;
    display: flex;
    padding: 1rem;
    border-radius: 1rem;
    background: rgba(255, 255, 255, .1);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, .2);
    transition: .2s linear;
}

.share a i {
    background: rgba(255, 255, 255, .2);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, .2);
    padding: 1rem 2rem;
    margin: 1rem;
    color: #fff;
    font-size: 4rem;
    border-radius: 1rem;
}

.share i:hover {
    background: rgba(255, 255, 255, .5);
    color: #555;
} 