.project{
    min-height: 50vh;
    padding: 1rem 5%;
    width: 80rem;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, .3);
    border-radius: 1rem;
    overflow: hidden;
    margin: 1rem 0 1rem 9rem;
    background-color: #0077b6;

}
.lag ul li{
    margin-top:2rem;
    font-size: 3rem;
}
.lag ul{
    display: inline-block;
}
.lag ul li a{
font-size: 1.5rem;
color: white;
padding: 1rem;
}
.lag ul li a:hover{
    background: rgba(255, 255, 255, .5);
    color: #555;
}
